.itemlist--container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 50px;
    border: black solid 1px;
    background-color: rgb(238, 238, 238);
}

@media (max-width: 400px) {
    .itemlist--container {
        border: none;
        background-color: white;
        margin: 10px;
    }
}